input {
  border: 1px solid black;
  overflow-y: scroll;
}

.header {
  padding: 16px 0 16px 0;
}

.header .logo {
  background-image: url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/compressed/5a-logo.svg.gz);
  background-repeat: no-repeat;
  background-position: cover;
  background-size: contain;
  width: 100%;
  height: 45px;
}

.header-menu > * {
  float: right;
  color: white;
  font-size: 1.1em;
  margin-left: 16px;
  padding: 10px 20px 10px 20px;
  cursor: pointer;
}

.header-menu-mobile {
  display: none;
}

.menu-mobile {
  display: none;
}

.header-menu-entrar {
  background-color: #50a0f0;
  padding: 10px 20px 10px 20px;
}
.header-menu-favoritos {
  padding: 10px 20px 10px 42px;
  background-image: url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/compressed/5a-favoritos-new.svg.gz);
  background-position: 0 6px;
  background-repeat: no-repeat;
  background-size: 42px 22px;
}

.header-menu-anunciar {
  border: 1px solid white;
  padding: 10px 40px 10px 40px;
}

.actions-grid {
  background-color: lightblue;
  background-image: url("http://d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/foto_home_desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100% 37%;
}

.rent {
  padding: 80px 0 120px 0;
  color: white;
}

.search-house-button {
  text-indent: -9999px;
  float: left;
  width: 70px;
  height: 50px;
  margin-left: 1px;
  background-color: #50a0f0;
  background-repeat: no-repeat;
  background-size: 27px 27px;
  background-position: 22px 12px;
  background-image: url("http://d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/compressed/5a-search-branco.svg.gz");
}

.search-house-input {
  width: 70%;
  float: left;
}

.search-house {
  margin: 16px auto 16px auto;
}

.app-grid {
  color: #555555;
  padding: 80px 0 80px 0;
  background-color: lightgray;
  background-image: url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/banner-iphone.png), url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/banner-android.png);
  background-repeat: no-repeat;
  background-position: 340px 20px, 200px 0;
  background-size: 167px 324px, 180px 325px;
}

.app-grid h1 {
  text-align: left;
}
.app-grid h2 {
  text-align: left;
}

.android-image {
    width: 180px;
    height: 325px;
    background-size: 100%;
    background-repeat: no-repeat;
}

.iphone-image {
    width: 167px;
    height: 324px;
    margin-left: -25px;
    margin-top: 15px;
    background-size: 100%;
    background-repeat: no-repeat;
}

.apple-image {
  background-image: url("http://d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/bagde_best_apple.png");
  background-repeat: no-repeat;
  height: 40px;
  width: 290px;
  background-size: 80% 80%;
}

.request-app > * {
  height: 40px;
}

.request-app-input {
  width: 60%;
}

.request-app-button {
  padding: 12px;
  margin-left: 2px;
  height: 20px;
  margin: 0;
  border: 1px solid darkgray;
}

.grid-funcionamento {
  margin-top: 40px;
}

.grid-funcionamento h2 {
  margin: 0;
}

.grid-funcionamento .sub-title {
  padding-bottom: 32px;
  border-bottom: 1px solid darkgray;
}

.grid-funcionamento h1 {
  margin-bottom: 20px;
  text-align: center;
  width: 100%;
}

.grid-funcionamento .feature-list {
  margin-top: 60px;
}

.grid-funcionamento .feature-list > * {
  padding: 20px;
}

.news-button {
  margin: 0;
  width: 150px;
  height: 60px;
  background-size: cover;
  background-image: url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/media-magazines.png);
}

.news-button.exame {
  background-position: 0 0;
}
.news-button.infomoney {
  width: 180px;
  background-position: -150px 0;
}
.news-button.estadao {
  background-position: -330px 0;
}
.news-button.pegn {
  width: 120px;
  background-position: -480px 0;
}

.grid-navegacao {
  background-color: #555555;
  padding-top: 40px;
  padding-bottom: 40px;
  background-image: url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/compressed/5a-logo.svg.gz);
  background-repeat: no-repeat;
  background-position: cover;
  background-size: 300px 150px;
}

ul.naveg li.title {
  margin-bottom: 2px;
  list-style: none;
  color: #999999;
}

ul.naveg li.title.mtop {
  padding-top: 4px;
}

ul.naveg li {
  font-weight: lighter;
  margin-bottom: 6px;
  list-style: none;
  color: white;
}

.footer {
  background-color: #333333;
  color: #666666;
}

.footer div {
  padding: 4px;
  width: 100%;
  text-align: center;
  font-size: 1.2em;
}

#App {
  padding: 0;
  margin: 0;
}

@media (max-width: 418px) {
  .search-house-input {
    width: 58%;
  }
}

@media (max-width: 1024px) {
  .app-grid {
    color: #555555;
    padding: 80px 0 80px 0;
    background-color: lightgray;
    background-image: none;
  }
}

@media (max-width: 768px) {
  .news-button {
    margin: auto;
  }
}

@media (max-width: 1200px) {
  .grid-navegacao {
    background-image: none;
  }
}

@media (max-width: 942px) {
  .header-menu {
    display: none;
  }
  .header-menu-mobile {
    display: inline;
  }
  .header-menu-mobile-favoritos {
    background-image: url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/compressed/5a-favoritos-new.svg.gz);
    background-position: 50% 0;
    background-repeat: no-repeat;
    background-size: 42px 22px;
  }

  .header-menu-mobile-menu {
    background-image: url(//d29k9pp6ge5sv1.cloudfront.net/searchStaticProducao/939/img/compressed/5a-menu-branco.svg.gz);
    background-position: 50% 0;
    background-repeat: no-repeat;
    background-size: 42px 22px;
    margin-left: 12px;
  }

  .header-menu-mobile > * {
    float: right;
    color: white;
    font-size: 1.1em;
    padding: 5px 0 5px 0;
    cursor: pointer;
    height: 50px;
    line-height: 5em;
  }

  .menu-mobile {
    display: block;
    position: fixed;
    float: right;
    width: 80%;
    height: 100vh;
    background-color: white;
    top: 0;
    left: 20px;

  }
}

@media (max-width: 432px) {
  .header .logo  {
    width: 90%;
  }

  
}


