body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 13px;
}

h1 {
  font-size: 3em;
  text-align: center;
  font-weight: normal;
  margin: 0;
}

h2 {
  font-size: 1.66em;
  text-align: center;
  font-weight: lighter;
}

h3 {
  font-size: 1.12em;
  text-align: center;
  font-weight: lighter;
}

form {
  width: 100%;
}

a {
  cursor: pointer;
}

input {
  padding: 0 8px 0 8px;
  border: 0 none transparent;
  height: 50px;
}


@media (max-width: 640px) {
  body {
    font-size: 10px;
  }
  h3 {
    font-size: 1.4em;
  }
}


